export default {
	props:{
		value:{
			type:String,
			default:null
		},
		//限制上传图片数量
		length:{
			type:Number,
			default:20
		},
		disabled:{
			type:Boolean,
			default:false
		},
		count:{
			type:Number,
			default:9
		}
	},
	data(){
		return {
			imgList:[]
		}
	},
	methods:{
		//上传图片
		upload(){
			let resideLength = this.length - this.imgList.length
			let count = this.count
			if(resideLength - count < 0){
				count = resideLength
			}
			uni.chooseImage({
			    count: count, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera'], //从相册选择或者拍摄
			    success: res => {
					let list = this.imgList.concat(res.tempFilePaths || [])
					this.imgList = list
					this.$emit('input',list.join(','))
			    }
			});
		},
		//删除某项
		delItem(index){
			this.imgList.splice(index,1)
			this.$emit('input',this.imgList.join(','))
		},
		//预览图片
		previewImage(index){
			uni.previewImage({
				current:index,
				urls:this.imgList,
				indicator:'default'
			})
		}
	},
	watch:{
		value:{
			handler(val){
				if(val){
					this.imgList = val.split(',').map((item,index)=>{
						return item
					})
				}
			},
			immediate:true,//使父组件传过来的默认值第一次监听到
		}
	}
}
